// 任务进度跟踪查询
export class addMarketingCampaignBean {
    campaignName: String; // 任务名称
    marketingObjectType: String; // 客户来源
    marketingCampaigntType: String; // 任务类型
    marketingContactTheme: String; // 联系主题(新增)
    marketingContactWay: String; // 联系方式(新增)
    fromDate: String; // 计划联系时间
    pushObject: String; // 推送类型
    remark: String; // 备注
    priority: String; // 优先级(新增)
    contactSource: String; // 计划来源(新增)
    thruDate: String;  // 营销活动的结束时间
    marMonitoringTimeFrom: String;  // 营销监控的开始时间
    marMonitoringTimeTo: String;  // 营销监控的结束时间
}

export class QueryCommunicationEventContactListBean {
    custId: string; //	客户号
    custName: string; //	客户名称
    marketingCampaignId: string; //	任务编号
    campaignName: string; //	任务名称
    marketingContactTheme: string; //	联系主题
    marketingContactWay: string; //	联系方式
    pushObject: string; //	推送类型
    pushDateStart: string; //	任务发布时间-起
    pushDateEnd: string; //	任务发布时间-止
    fromDateStart: string; //	计划联系时间-起
    fromDateEnd	: string; //计划联系时间-止
    thruDateStart: string; //	任务截止时间-起
    thruDateEnd	: string; //任务截止时间-止
    processUserId: string; //	处理人
    statusId: string; //	处理状态
    contactResult	: string; //处理结果
    contactTimeStart: string; //	处理时间-起
    contactTimeEnd: string; //	处理时间-止
    contactSource: string; //	计划来源
    pageNum: number = 1; //	页码
    pageSize: number = 10; //	页数
    
}
