import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Output } from "@angular/core";
import { EventEmitter } from "@angular/core";
import { QueryConWorkEffortDetailInfoBean } from 'app/pages/tzb/custom/custom-contact-new/custom-contact-task/custom-speed-track/bean/custom-speed-track.bean';
import { ContactInformation, LinkageTheme, PriorityQuery, TaskStatusQuery, TourcesPlanning, CustomerSource, PushType } from '../../../custom-constant/custom-code-value';

@Component({
  selector: 'custom-speed-track-details',
  templateUrl: './custom-speed-track-details.component.html',
  styleUrls: ['./custom-speed-track-details.component.scss']
})
/**
 * 客户联系进度跟踪详情
 */
export class CustomSpeedTrackDetailsComponent implements OnInit, OnChanges {

  @Input() inValue: any;
  @Output() outValue = new EventEmitter();

  ch: any = CALENDAR_CN;
  deployTimeStart: any;
  contactWay: any;
  msgs: any;
  deployTimeEnd: any;
  workEffortId: any; //	工作任务ID
  communicationEventId: any; //	沟通事件ID
  showFlagSubmit: boolean = true; //	沟通事件ID

  contactInformation: any = ContactInformation // 联系方式
  linkageTheme: any = LinkageTheme; // 联系主题
  taskStatus: any = TaskStatusQuery; // 工作任务当前状态
  triority: any = PriorityQuery; // 优先级
  tourcesPlanning: any = TourcesPlanning; // 计划来源
  customerSource: any = CustomerSource; // 客户来源
  pushType: any = PushType; // 推送类型
  showContractNum: boolean = false;
  queryConWorkBean: QueryConWorkEffortDetailInfoBean = new QueryConWorkEffortDetailInfoBean()

  constructor(
    public httpService: CusOperationHttpService,
  ) {
    console.log(this.inValue);
  }

  ngOnInit() {
  }

  ngOnChanges() {
    this.workEffortId = this.inValue.marketingCampaignId//	工作任务ID
    this.communicationEventId = this.inValue.communicationEventId //	沟通事件ID
    this.queryConWorkEffortDetailInfo()
  }

  doReturn() {
    this.outValue.emit(false);
  }

  // 客户联系进度跟踪查询
  queryConWorkEffortDetailInfo() {
    let param = {
      communicationEventId: this.communicationEventId, //	沟通事件ID
    }
    this.httpService.queryConWorkEffortDetailInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.queryConWorkBean = data
        // 信贷类 02 11 12 14
        if (this.queryConWorkBean.marketingContactTheme == '02' || this.queryConWorkBean.marketingContactTheme == '11' || this.queryConWorkBean.marketingContactTheme == '12' || this.queryConWorkBean.marketingContactTheme == '14') {
          this.showContractNum = true;
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

}
