  // 客户联系导入名单查询 第三页
  queryDirectContactData(col) {

    let param = {
      marketingCampaignId: this.marketingCampaignId, //	活动ID
      statusId: col, //	状态
      pageNum: this.pageNum, //	页码
      pageSize: this.pageSize, //	页数
    }
    this.loadingShowFlag = true;
    this.httpService.queryDirectContactData(param).subscribe(data => {
      this.loadingShowFlag = false;
      console.log("客户联系导入名单查询出参")
      console.log(data)
      if (data.returnCode.code == 'success') {
        this.successTotalCount = data.successTotalCount; // 成功总条数
        this.failTotalCount = data.failTotalCount; // 失败总条数
        this.totalTwo = data.totalNum
        if (data.resultList) {
          this.mapList = data.resultList
        } else {
          this.mapList = [];
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.loadingShowFlag = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

