  // 文件上传前回调
  beforeSend(event) {
    // 防止重复上传文件档，重复读取
    this.batchDelDirectContactDataById({ marketingCampaignId: this.marketingCampaignId })
  }

  // 客户联系导入名单批量删除
  batchDelDirectContactDataById(col) {
    let param = {
      requerList: col.requerList,
      marketingCampaignId: col.marketingCampaignId,
    }
    this.httpService.batchDelDirectContactDataById(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '删除成功' });
        this.queryDirectContactData('0')
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 任务活动发布审批 第四页
  approveMarketingCampaignExt() {
    if (!this.nodeUserId) {
      this.msgs = [];
      this.msgs.push({ severity: 'info', summary: '提示', detail: '请选择审批人，再进行提交！' });
      return;
    }
    // 将审批人id放入报文头中
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = this.nodeUserId;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

    let param = {
      marketingCampaignId: this.marketingCampaignId,//	任务编号
      dedId: "",//	主键ID
      // resultList: this.custIdSuccessList,
    }
    this.httpService.approveMarketingCampaignExt(param).subscribe(data => {
      // 将审批id删掉
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: "已提交审批，审批任务请查看我的待办。审批通过后会予以发布！" });
      setTimeout(() => {
        // 清空数据
        this.reset();
      }, 2000);
      this.finsh = true;
      if (data.returnCode.code == 'success') {
        this.finsh = false;
      } else {
        this.finsh = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.finsh = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 推送名单按钮切换
  pushClick(code) {
    // 推送名单
    if (code == 1) {
      this.showFlagPush = true;
      this.queryDirectContactData('0')
    } else {
      // 上传失败名单
      this.showFlagPush = false;
      this.queryDirectContactData('1')
    }
  }

