import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { SpeedTrack, LinkageTheme, ContactInformation, PushType, ProcessingResults, StatusId } from '../../custom-constant/custom-code-value';
import { QueryCommunicationEventContactListBean } from './bean/custom-speed-track.bean';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { TZB_CUS_DOWNLOAD } from '../../../../../../@uisftech/common/service/http.constant';
import { ActivatedRoute } from '@angular/router';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'custom-speed-track',
  templateUrl: './custom-speed-track.component.html',
  styleUrls: ['./custom-speed-track.component.scss']
})
export class CustomSpeedTrackComponent implements OnInit {

  items: any = [];
  activeIndex: number = 0;
  setInvalue: any = SpeedTrack // 列表字段

  mapList: any = [] // 列表
  showFlagField: Boolean = false; // 选择字段开关
  display: boolean = false;
  showFlagDetails: boolean = false; // 详情页面开关
  deployTimeStart: Date = null;//
  deployTimeEnd: Date = null;//
  planContactTimeStart: Date = null;
  planContactTimeEnd: Date = null;
  msgs: Message[] = [];
  ch: any = CALENDAR_CN;
  contactWay: any;
  tableMesg: any;
  total: any;
  first: any = 1;
  pageSize: any;
  hrOrgName: any;
  personPage: boolean = false;
  userInValue: any;
  userId: string; //  用户编号
  rzjg: string;//任职机构
  personName: string; // 姓名
  inValue: any; //详情传参
  userOrgId: any; //转分配用户所属机构
  userOrgName: any; //转分配用户所属机构名
  selectedValues: any = [];
  setList: any = []; // 字段集合
  allList: any = []; // 选择字段集合
  selectedList: any = []; // 选中字段集合
  tatch: boolean = false; // 分配标识
  detailList: any;

  DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
  linkageTheme: any = LinkageTheme; //联系主题
  contactInformation: any = ContactInformation; //联系方式
  pushType: any = PushType; //推送类型
  processingStarts: any = StatusId; //处理状态
  processingResults: any = ProcessingResults; //处理结果
  HeaderTitle: any = '';
  contactList: QueryCommunicationEventContactListBean = new QueryCommunicationEventContactListBean();

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public routeInfo: ActivatedRoute,
    public datePipe: DatePipe,
  ) { }

  ngOnInit() {


    this.detailList = this.routeInfo.snapshot.params;
    if (this.detailList) {
      this.contactList.marketingCampaignId = this.detailList.marketingCampaignId
    }
    console.log(this.detailList);


    this.codeVal()
    this.queryCommunicationEventContactList()
    this.setList = this.setInvalue
    this.allList = this.setInvalue
    this.selectedList = this.setInvalue

    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.userId = s.userId;
    console.log(s);
    // if (s.extParam.orgId) {
    //   this.rzjg = s.extParam.orgId;
    // }
  }

  //码值
  codeVal() {
    let param = {
      // paramGroupId: "COP",
      // description: "客户联系岗位配置",
      paramName: "custContactPostName",
      // paramTypeId: "business",
      // paramId: "196000003200000",
      // paramValue: "custContactPostName",
      // status: "start",
      // limit: "10",
    }
    this.httpService.queryCommonParamItemService(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.returnCommonParamItemList.forEach(element => {
          this.pushType.push({
            label: element.paramItemValue,
            value: element.paramItemName
          })
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 客户联系进度跟踪查询
  queryCommunicationEventContactList() {
    this.contactList.pushDateStart = this.datePipe.transform(this.contactList.pushDateStart, 'yyyy-MM-dd');
    this.contactList.pushDateEnd = this.datePipe.transform(this.contactList.pushDateEnd, 'yyyy-MM-dd');
    this.contactList.fromDateStart = this.datePipe.transform(this.contactList.fromDateStart, 'yyyy-MM-dd');
    this.contactList.fromDateEnd = this.datePipe.transform(this.contactList.fromDateEnd, 'yyyy-MM-dd');
    this.contactList.thruDateStart = this.datePipe.transform(this.contactList.thruDateStart, 'yyyy-MM-dd');
    this.contactList.thruDateEnd = this.datePipe.transform(this.contactList.thruDateEnd, 'yyyy-MM-dd');
    this.contactList.contactTimeStart = this.datePipe.transform(this.contactList.contactTimeStart, 'yyyy-MM-dd');
    this.contactList.contactTimeEnd = this.datePipe.transform(this.contactList.contactTimeEnd, 'yyyy-MM-dd');

    let param = this.contactList
    this.httpService.queryCommunicationEventContactList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.mapList = data.resultList;
        this.total = data.totalNum;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 重置
  reset() {
    this.personName = '';
    this.selectedValues = [];
    this.contactList = new QueryCommunicationEventContactListBean();
    this.contactList.pageSize = 10;
    this.contactList.pageNum = 1;
    this.first = 0;
    this.queryCommunicationEventContactList();
  }

  // 分页
  paginate(event) {
    this.contactList.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.contactList.pageNum = currentPage;
    this.first = (currentPage - 1) * this.contactList.pageSize;
    this.queryCommunicationEventContactList();
  }

  //选择员工
  chooseWorker() {
    // document.body.style.overflow = 'hidden';
    this.personPage = !this.personPage;
    this.userInValue = this.rzjg;
  }

  //员工回调
  perInfor(item) {
    console.log(item)
    // document.body.style.overflow = 'auto';
    this.personPage = false;
    if (item.length > 0) {
      this.contactList.processUserId = item[0].tellerId; // 员工编号
      this.personName = item[0].tellerName; // 姓名
      this.userOrgId = item[0].orgId; // 机构号
      this.userOrgName = item[0].orgName; // 机构名
    }
    if (this.tatch) {
      // 调用批量分配
      this.distributeContactWorkEffort()
    }
  }

  // 详情
  details(col) {
    this.HeaderTitle = '详情';
    this.display = true;
    this.showFlagDetails = true;
    this.showFlagField = false;
    this.inValue = col;
  }
  // 详情弹框回调
  detailCall(event) {
    if (event == false) {
      console.log(event);
      this.showFlagDetails = event;
      this.display = event;
    }
  }


  // 选择字段回调
  addUserDefinedSet(event) {
    console.log('选择字段回调')
    console.log(event)
    this.display = event.display;
    this.showFlagField = false;
    if (event && event.selectedList && event.selectedList.length > 0) {
      this.setList = event.selectedList;
    }

  }

  // 选择弹框方法
  selectFeil() {
    console.log(123)
    this.HeaderTitle = '选择字段';
    this.display = true;
    this.showFlagField = true;
    this.showFlagDetails = false;
    this.selectedList = this.setList
  }

  // 手动输入日期
  handInputDate(param1, param2) {
  }

  // 客户联系成效统计导出
  queryCommunicationEventContactExport() {
    let param = this.contactList;
    param['userName'] = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName
    this.httpService.queryCommunicationEventContactExport(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl, "_self")
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 批量分配
  tatchClick() {
    // 打开员工选择
    this.chooseWorker();
    this.tatch = true;
  }

  // 批量分配
  distributeContactWorkEffort() {

    let param = {
      userLoginId: this.contactList.processUserId, //	转分配用户ID
      userLoginName: this.personName, //	转分配用户姓名
      communicationEventIdList: this.selectedValues, //	沟通事件ID
      userOrgId: this.userOrgId, //转分配用户所属机构
      userOrgName: this.userOrgName, //转分配用户所属机构名
    }
    this.httpService.distributeContactWorkEffort(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tatch = false;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '分配成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 开始、结束日期判断
  showPushDateStartTime() {
    let flag = this.commfunc.judgeexpireDate(this.contactList.pushDateStart, this.contactList.pushDateEnd)
    console.log(flag)
    if (flag) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
      this.contactList.pushDateEnd = null;
    }
  }
  showFromDateTime() {
    let flag = this.commfunc.judgeexpireDate(this.contactList.fromDateStart, this.contactList.fromDateEnd)
    console.log(flag)
    if (flag) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
      this.contactList.fromDateEnd = null;
    }
  }
  showThruDateTime() {
    let flag = this.commfunc.judgeexpireDate(this.contactList.thruDateStart, this.contactList.thruDateEnd)
    console.log(flag)
    if (flag) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
      this.contactList.thruDateEnd = null;
    }
  }
  showContactTime() {
    let flag = this.commfunc.judgeexpireDate(this.contactList.contactTimeStart, this.contactList.contactTimeEnd)
    console.log(flag)
    if (flag) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
      this.contactList.contactTimeEnd = null;
    }
  }

  onHideClose() {

  }

}
