import { Component, OnInit, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import * as _ from 'lodash';
import { Router, NavigationEnd } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import { NavigationStart } from '@angular/router';
import { ChildActivationStart } from '@angular/router';

@Component({
  selector: 'custom-contact-task',
  templateUrl: './custom-contact-task.component.html',
  styleUrls: ['./custom-contact-task.component.scss']
})
export class CustomContactTaskComponent implements OnInit, OnChanges {

  // 路由常量
  CUSTOMURL: any = {
    CUSTOM: '/pages/tzb/custom/custom-contact-new/custom-contact-task/custom-speed-track', // 客户联系进度跟踪
    RELASE: '/pages/tzb/custom/custom-contact-new/custom-contact-task/relase-speed-tarck', // 任务进度跟踪
    CONTACT: '/pages/tzb/custom/custom-contact-new/custom-contact-task/release-custom-contact-task', // 发布客户联系
    STAFF: '/pages/tzb/custom/custom-contact-new/custom-contact-task/staff-speed-track', // 员工处理进度跟踪
  }

  items: any = [];
  index: number = 0;
  inValue: any;
  inValueTwo: any;
  detailList: any;

  constructor(
    public httpService: CusOperationHttpService,
    public router: Router,
    public routeInfo: ActivatedRoute,
  ) { }

  ngOnInit() {
    // 路由监听，同步tabView页的同步
    this.router.events.subscribe((event: NavigationEnd) => {
      console.log(event)
      if (event && event.url) {
        // 获取路由
        let eventUrl = event.url.split(';')[0];
        console.log(eventUrl)
        if (typeof eventUrl !== 'undefined' && eventUrl !== "") {
          // 判断路由，同步tab页面的索引
          if (eventUrl == this.CUSTOMURL.CUSTOM) { // 客户联系进度跟踪
            this.index = 3
          } else if (eventUrl == this.CUSTOMURL.RELASE) { // 任务进度跟踪
            this.index = 1
          } else if (eventUrl == this.CUSTOMURL.CONTACT) { // 发布客户联系
            this.index = 0
          } else if (eventUrl == this.CUSTOMURL.STAFF) { // 员工处理进度跟踪
            this.index = 2
          }
        }
      }
    })
  }

  ngOnChanges() {
    // this.inValueTwo = this.routeInfo.snapshot.params;
    console.log(this.inValueTwo);
    
  }

  onTabChange(event) {
    console.log(event)
    // this.inValueTwo = event.index
    if (event.index == 0) {
      this.router.navigate([this.CUSTOMURL.CONTACT])
    } else if (event.index == 1) {
      this.router.navigate([this.CUSTOMURL.RELASE])
    } else if (event.index == 2) {
      this.router.navigate([this.CUSTOMURL.STAFF])
    } else if (event.index == 3) {
      this.router.navigate([this.CUSTOMURL.CUSTOM])
    }
  }

  // 任务发布回调
  rest(event) {
    console.log(event)
    this.index = event.code;
    this.inValueTwo = 2
    // setTimeout(() => {
    //   this.index = 0
    // }, 2000);
  }

  // 任务进度跟踪回调
  updateeqwe(event) {
    // this.rest(event)
    // console.log(event)
    this.index = 1;
      setTimeout(() => {
        this.index = 0
      }, 1000);
    // this.index = 0;
    console.log(this.index)
    this.inValue = event.params;
  }


}
