import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { QueryContactMarketingCampaignListBean } from '../bean/staff-speed-track.bean';
import { LinkageTheme, ContactInformation } from '../../../custom-constant/custom-code-value';
import { Input } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { Output } from '@angular/core';

@Component({
  selector: 'staff-speed-track-query',
  templateUrl: './staff-speed-track-query.component.html',
  styleUrls: ['./staff-speed-track-query.component.scss']
})
export class StaffSpeedTrackQueryComponent implements OnInit {

  @Input() inValue: any;
  @Output() outValue = new EventEmitter();

  mapList: any = []; //列表数据
  tableMesg: any = []; //列表数据
  custIdSuccessList: any = []; //列表数据
  total: any = []; //列表数据
  pageSize: any = []; //列表数据
  first: any = []; //列表数据
  msgs: Message[] = [];
  linkageTheme: any = LinkageTheme; // 联系主题
  contactInformation: any = ContactInformation; // 联系方式

  campaignListBean: QueryContactMarketingCampaignListBean = new QueryContactMarketingCampaignListBean()

  constructor(
    public httpService: CusOperationHttpService,
  ) { }

  ngOnInit() {
    this.campaignListBean.pageSize = 10;
    this.campaignListBean.pageNum = 1;
    this.first = 1;
    this.queryContactMarketingCampaignList()
  }

  // 重置
  reset() {
    this.campaignListBean = new QueryContactMarketingCampaignListBean();
    this.campaignListBean.pageSize = 10;
    this.campaignListBean.pageNum = 1;
    this.first = 1;
  }

  queryCommunicationEventUserStatistics() {
    this.queryContactMarketingCampaignList()
  }

  paginate(event) {
    this.campaignListBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.campaignListBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.campaignListBean.pageSize;
    this.queryContactMarketingCampaignList()
  }

  // 确认按钮
  subimt() {
    this.outValue.emit({ list: this.custIdSuccessList, display: false })
  }

  // 员工处理进度跟踪查询
  queryContactMarketingCampaignList() {
    let param = this.campaignListBean
    this.httpService.queryContactMarketingCampaignList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        console.log('3222' + data + '2333')
        this.mapList = data.resultList;
        this.total = data.totalNum;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

}
