import { Component, OnInit, OnChanges, Input } from '@angular/core';
import { CALENDAR_CN } from '../../../../../../@uisftech/common/constant/config';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { tarckLIst, LinkageTheme, ContactInformation, Priority, TaskStatus, ActiveState, ActiveStateTwo } from '../../custom-constant/custom-code-value';
import { MarketingCampaignFromContactBean } from './bean/relase-speed-tarck.bean';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { EventEmitter } from '@angular/core';
import { Output } from '@angular/core';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'relase-speed-tarck',
  templateUrl: './relase-speed-tarck.component.html',
  styleUrls: ['./relase-speed-tarck.component.scss']
})
export class RelaseSpeedTarckComponent implements OnInit, OnChanges {

  @Input() inValue: any;
  @Input('inValueTwo') inValueTwo: any;
  @Output() outValueTarck = new EventEmitter();

  items: any = [];
  activeIndex: number = 0;
  ch: any = CALENDAR_CN;
  deployTimeEnd: Date = null;
  planContactTimeStart: Date = null;
  deployTimeStart: Date = null;
  msgs: Message[] = [];
  planContactTimeEnd: Date = null;
  mapList: any = [];
  display: boolean = false;
  orgInValue: any; // 机构
  orgTreeDisplay: boolean = false;//机构树模态框
  custMajorName: string = ''; // 机构名
  total: string; // 总条数
  first: number = 0; //起始页
  allList: any; // 全量数据
  selectedList: any;// 选中数据

  setInvalue: any = tarckLIst; // 列表字段
  linkageTheme: any = LinkageTheme; //联系主题
  contactInformation: any = ContactInformation; //联系方式
  pushTypes: any = [
    { value: '10', label: '主联系人' },
  ]; //推送类型
  priority: any = Priority; //优先级
  taskStatus: any = ActiveStateTwo; //工作任务当前状态
  personPage: boolean = false;
  userInValue: any;
  rzjg: string;//任职机构
  personName: string;
  userId: string;
  tableMesg: string;
  hrOrgName: string;
  contactWay: string;
  detailInValue: any;
  detailDisplay: boolean = false;

  contactBean: MarketingCampaignFromContactBean = new MarketingCampaignFromContactBean(); // 查询对象

  constructor(
    public router: Router,
    public confirmationService: ConfirmationService,
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public datePipe: DatePipe,
  ) { }

  ngOnInit() {
    this.allList = this.setInvalue; // 全量数据
    this.selectedList = this.setInvalue;// 选中数据

    // 查询任务活动处理进度
    this.codeVal();
    this.queryMarketingCampaignFromContact();

    // 获取客户id
    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    console.log(s)
    this.contactBean.userId = s.userId;
    this.custMajorName = JSON.parse(this.commfunc.getSessionDataCH('chName')).orgList[0].orgName;
    this.contactBean.createUserOrg = s.orgId;
    console.log(this.contactBean.userId);
    // if (s.extParam.orgId) {
    //   this.rzjg = s.extParam.orgId;
    // }
    // 获取用户名
    this.personName = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;
    console.log(this.personName);
  }

  ngOnChanges() {
    console.log("hahahaha");
    console.log(this.inValueTwo);
    console.log(this.inValue);
    // 查询任务活动处理进度
    this.queryMarketingCampaignFromContact();
  }

  //码值
  codeVal() {
    let param = {
      // paramGroupId: "COP",
      // description: "客户联系岗位配置",
      paramName: "custContactPostName",
      // paramTypeId: "business",
      // paramId: "196000003200000",
      // paramValue: "custContactPostName",
      // status: "start",
      // limit: "10",
    }
    this.httpService.queryCommonParamItemService(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.returnCommonParamItemList.forEach(element => {
          this.pushTypes.push({
            label: element.paramItemValue,
            value: element.paramItemName
          })
        });
        console.log(this.pushTypes)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //选择员工
  chooseWorker() {
    document.body.style.overflow = 'hidden';
    this.personPage = !this.personPage;
    this.userInValue = this.rzjg;
  }

  // 重置
  reset() {
    this.custMajorName = '';
    this.contactBean = new MarketingCampaignFromContactBean();
  }

  //员工回调
  perInfor(item) {
    document.body.style.overflow = 'auto';
    this.personPage = false;
    if (item.length > 0) {
      this.contactBean.createUserId = item[0].tellerId; // 员工编号
      this.personName = item[0].tellerName; // 姓名
    }
  }

  //选择机构
  chooseOrg() {
    document.body.style.overflow = 'hidden';
    this.orgInValue = 'noShow';
    this.orgTreeDisplay = !this.orgTreeDisplay;
  }

  //机构树回调
  treeCall(param) {
    document.body.style.overflow = 'auto';
    this.orgTreeDisplay = false;
    this.custMajorName = param.orgName; // 机构名
    this.contactBean.createUserOrg = param.orgId;  //机构id
  }

  upDate(col) {
    // console.log(col)
    // this.outValueTarck.emit({code: 2, params: col})
    this.router.navigate(['/pages/tzb/custom/custom-contact-new/custom-contact-task/release-custom-contact-task', col])
  }

  // 删除
  deleteTarck(col) {
    console.log(col)
    this.confirmationService.confirm({
      message: '确认删除该名单?',
      accept: () => {
        let param = {
          marketingCampaignId: col.marketingCampaignId,// 联系策略标识
        };
        this.httpService.deleteMarketingCampaignFromContact(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: "删除成功" });
            this.queryMarketingCampaignFromContact()
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  // 终止
  termination(col) {
    console.log(col)
    this.confirmationService.confirm({
      message: '确认终止该名单?',
      accept: () => {
        let param = {
          marketingCampaignId: col.marketingCampaignId,// 联系策略标识
        };
        this.httpService.stopMarketingCampaignFromContact(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '终止成功' });
            this.queryMarketingCampaignFromContact();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }

  // 查询按钮
  query() {
    this.contactBean.pageSize = 10;
    this.contactBean.pageNum = 1;
    this.first = 0;
    this.queryMarketingCampaignFromContact();
    console.log(this.contactBean);
  }

  // 查询任务活动处理进度
  queryMarketingCampaignFromContact() {
    this.contactBean.fromDateStart = this.datePipe.transform(this.contactBean.fromDateStart, 'yyyy-MM-dd HH:mm')
    this.contactBean.fromDateEnd = this.datePipe.transform(this.contactBean.fromDateEnd, 'yyyy-MM-dd HH:mm')
    this.contactBean['marketingContactWayList'] = this.contactBean.marketingContactWay
    this.contactBean['marketingContactThemeList'] = this.contactBean.marketingContactTheme
    this.contactBean['pushObjectList'] = this.contactBean.pushObject
    console.log(this.contactBean);
    let param = this.contactBean
    this.httpService.queryMarketingCampaignFromContact(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.mapList = data.resultList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 详情按钮
  details(col) {
    // this.router.navigate(['/pages/tzb/custom/custom-contact-new/relase-speed-tarck-details',{marketingCampaignId: col.marketingCampaignId}])
    this.detailInValue = col;
    this.detailDisplay = !this.detailDisplay;
  }

  //分页
  paginate(event) {
    this.contactBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.contactBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.contactBean.pageSize;
    this.queryMarketingCampaignFromContact();
  }
  onHideClose() {

  }

  // 选择字段回调
  addUserDefinedSet(event) {
    console.log('选择字段回调')
    console.log(event)
    this.display = event.display;
    if (event && event.selectedList && event.selectedList.length > 0) {
      this.setInvalue = event.selectedList;
    }
  }

  // 选择弹框方法
  selectFeil() {
    this.display = true;
    this.selectedList = this.setInvalue;// 选中数据
  }

  // 手动输入日期
  handInputDate(param1, param2) {
  }

  // 开始、结束日期判断
  showTime() {
    let flag = this.commfunc.judgeexpireDate(this.contactBean.fromDateStart, this.contactBean.fromDateEnd)
    console.log(flag)
    if (flag) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
      this.contactBean.fromDateEnd = null;
    }
  }

}
