import { Component, OnInit, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { FaileDirectContactDataBean } from '../bean/release-custom-contact-task.bean';
import { Output } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { Input } from '@angular/core';

@Component({
  selector: 'release-custom-contact-task-modify',
  templateUrl: './release-custom-contact-task-modify.component.html',
  styleUrls: ['./release-custom-contact-task-modify.component.scss']
})
export class ReleaseCustomContactTaskModifyComponent implements OnInit, OnChanges {
  finsh: boolean = false;
  msgs: Message[] = [];
  @Output() outValue = new EventEmitter();
  // @Input() inValue: any = {};

  @Input() contactDataBean: FaileDirectContactDataBean = new FaileDirectContactDataBean(); // 客户联系校验失败名单修改

  constructor(
    public httpService: CusOperationHttpService,
  ) { }

  ngOnInit() {
    this.contactDataBean = new FaileDirectContactDataBean();
    console.log(this.contactDataBean)
  }

  ngOnChanges() {


  }

  reset() {
    this.outValue.emit({ display: false });
  }

  // 客户联系导入名单修改
  updateFaileDirectContactData() {
    let param = this.contactDataBean
    console.log("客户联系导入名单查询入参")
    console.log(param)
    this.finsh = true;
    this.httpService.updateFaileDirectContactData(param).subscribe(data => {
      console.log("客户联系导入名单查询出参")
      console.log(data)
      if (data.returnCode.code == 'success') {
        this.finsh = false;
        this.outValue.emit({ display: false, code: true });
      } else {
        this.finsh = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.finsh = false;      
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

}
